<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <SysCommonTree
      treeClass="left-tree-layout"
      :toolbar="true"
      :search="true"
      title="表单设计"
      treeType="sys_form_design"
      @select="handleSelect"
      :beforeDelete="beforeDelete"
    />
    <!-- 数据表格 -->
    <div class="right-table-layout">
      <BasicTable
        @register="registerTable"
        class="fix-basic-table"
        :searchInfo="searchInfo"
        :resizeHeightOffset="0"
      >
        <template #toolbar>
          <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreateIndex">
            {{ t('common.action.create') }}
          </a-button>
          <a-button type="primary" :preIcon="IconEnum.EDIT" @click="handleUpdateIndex">{{
            t('common.action.edit')
          }}</a-button>
          <!-- <a-button class="red-btn" :preIcon="IconEnum.DELETE" @click="handleDelete">{{
          t('common.delText')
        }}</a-button> -->
          <a-button class="green-btn" :preIcon="IconEnum.ENABLE" @click="handleEnableData"
            >启用</a-button
          >
          <a-button class="grey-btn" :preIcon="IconEnum.DISABLE" @click="handleDisableData"
            >禁用</a-button
          >
          <a-button type="primary" @click="handleMoveIndex">移动</a-button>
        </template>
        <template #bodyCell="{ column, text }">
          <template v-if="column.dataIndex === 'status'">
            <span style="color: #19be6b" v-if="text == 1">启用</span>
            <span style="color: red" v-if="text == 0">禁用</span>
          </template>
        </template>
      </BasicTable>
    </div>

    <!-- 新增修改弹窗 -->
    <Dialog
      v-model:visible="formDesignModal"
      title="添加表单设计"
      :maskClosable="false"
      @ok="handleSaveFrom"
      :height="650"
      :width="600"
      smode="normal"
      @cancel="handleClosedModal"
    >
      <a-form ref="fromRef" :model="formState" name="userForm" :bordered="false">
        <a-row :gutter="24">
          <a-col v-show="true" :span="23">
            <a-form-item
              label="编码"
              name="code"
              :labelCol="{ span: 6 }"
              :rules="[{ required: true, max: 30 }]"
            >
              <a-input v-model:value="formState.code" :maxlength="64" />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="名称"
              name="name"
              :labelCol="{ span: 6 }"
              :rules="[{ required: true, max: 30 }]"
            >
              <a-input v-model:value="formState.name" :maxlength="64" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="json"
              name="jsonData"
              :labelCol="{ span: 6 }"
              :rules="[{ required: true }]"
            >
              <a-textarea v-model:value="formState.jsonData" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="URL"
              name="loadUrl"
              :labelCol="{ span: 6 }"
              :rules="[{ required: true, max: 1000 }]"
            >
              <a-input v-model:value="formState.loadUrl" :maxlength="64" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="明细URL"
              name="detailUrl"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, max: 1000 }]"
            >
              <a-input v-model:value="formState.detailUrl" :maxlength="250" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="明细字段"
              name="detailField"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, max: 1000 }]"
            >
              <a-input v-model:value="formState.detailField" :maxlength="250" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              label="备注"
              name="remark"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, max: 1000 }]"
            >
              <a-input v-model:value="formState.remark" :maxlength="64" />
            </a-form-item>
          </a-col>
        </a-row>

        <a-row>
          <a-col v-show="true" :span="23">
            <a-form-item
              name="status"
              label="状态"
              :labelCol="{ span: 6 }"
              :rules="[{ required: true, message: '请选择状态' }]"
            >
              <DictRadioGroup v-model:value="formState.status" :type="`enable_or_disable`" />
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </Dialog>

    <!-- 移动弹窗 -->
    <Dialog
      v-model:visible="moveModal"
      title="移动表单设计"
      :maskClosable="false"
      @ok="handleSaveMoveFrom"
      :toolbar="false"
      :search="false"
      :height="650"
      :width="600"
      smode="normal"
    >
      <a-form ref="moveFromRef" :model="formState" name="userForm">
        <SysCommonTree
          title="移动表单设计"
          treeClass="w-6/6"
          treeType="sys_form_design"
          @select="handleMoveSelect"
        />
      </a-form>
    </Dialog>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { reactive, ref } from 'vue';
  import { BasicTable, useTable, SorterResult } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import SysCommonTree from '@/views/system/configuration/commontree/SysCommonTree.vue';
  import { message, FormInstance } from 'ant-design-vue';
  import { assign } from 'lodash-es';
  import DictRadioGroup from '@/components/Framework/Radio/DictRadioGroup.vue';
  import {
    columns,
    searchFormSchema,
    saveFormDesign,
    deleteFormDesign,
    getFormDesignList,
    updateStatus,
  } from './data';
  import { useI18n } from '@/hooks/web/useI18n';
  import { IconEnum } from '@/enums/appEnum';
  import { useMessage } from '@/hooks/web/useMessage';
  import Dialog from '@/components/Framework/Modal/Dialog.vue';

  const { createConfirm } = useMessage();
  const { t } = useI18n();
  // 查询信息
  const searchInfo = reactive<any>({});
  const formState = ref<any>({});
  // 被选择的树节点
  const selectedNode = reactive<any>({});
  const formDesignModal = ref<boolean>(false);
  const selectedMoveNode = reactive<any>({});
  const moveModal = ref<boolean>(false);
  const fromRef = ref<FormInstance>();
  const indexList = ref<Array<any>>([]);

  // 左侧树状菜单选中事件
  function handleSelect(node) {
    searchInfo.typeFullId = null;
    if (node) {
      searchInfo.typeFullId = node.fullId;
      searchInfo.folderId = node.id;
      searchInfo.folderName = node.name;
      selectedNode.folderId = node.id;
      selectedNode.nodeType = node.parentId;
      selectedNode.existChildNode = node.children.length > 0;
    }
    if (node.parentId == 'root') {
      searchInfo.folderId = '';
    }
    reload();
  }

  // 树删除
  function beforeDelete(node) {
    if (indexList.value.total > 0) {
      message.warn('该节点存在子数据,不能删除。');
    }
    return indexList.value.total == 0;
  }

  // 添加
  async function handleCreateIndex() {
    if (!selectedNode.folderId) {
      message.error('请选择树节点类型。');
      return;
    }
    if (selectedNode.nodeType == 'root') {
      message.error('根节点不能添加业务数据。');
      return;
    }
    formState.value = {};
    formState.value.folderId = selectedNode.folderId;
    formState.value.status = '1';
    formDesignModal.value = true;
  }

  //获取表格选择数据
  function checkSelectData() {
    const selectRows = getSelectRows();
    if (selectRows.length == 0) {
      message.error('请选择一行数据。');
      return false;
    }
    return selectRows[0] as any;
  }

  // 修改
  function handleUpdateIndex() {
    const row = checkSelectData();
    if (!row.id) {
      return;
    }
    formState.value = row;
    formState.value.status = String(row.status);
    formDesignModal.value = true;
  }

  //保存
  function handleSaveFrom() {
    fromRef.value.validateFields().then(() => {
      saveFormDesign(formState).then(() => {
        formDesignModal.value = false;
        message.success('操作成功。');
        reload();
      });
    });
  }

  //关闭弹框
  function handleClosedModal() {
    formDesignModal.value = false;
  }

  // 删除
  async function handleDelete() {
    const row = checkSelectData();
    if (!row.id) {
      return;
    }
    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        deleteFormDesign(row.id).then(() => {
          message.success('已删除');
          clearSelectedRowKeys();
          reload();
        });
      },
    });
  }

  // 启用
  function handleEnableData() {
    checkStatus(1);
  }

  // 禁用
  function handleDisableData() {
    checkStatus(0);
  }

  function checkStatus(newStatus) {
    const row = checkSelectData();
    if (!row.id) {
      return;
    }
    if (row.status == newStatus) {
      message.success(row.status == 0 ? '该数据已禁用无须操作。' : '该数据已启用无须操作。');
      reload();
    } else {
      updateStatus(row.id, newStatus).then(() => {
        message.success(newStatus == 0 ? '已禁用。' : '已启用。');
        reload();
        clearSelectedRowKeys();
      });
    }
  }

  //  移動
  function handleMoveIndex() {
    const row = checkSelectData();
    if (!row.id) {
      return;
    }
    moveModal.value = true;
  }

  function handleSaveMoveFrom() {
    const row = checkSelectData();
    if (selectedMoveNode.nodeType == 'root') {
      message.error('根节点不能添加业务数据。');
      return;
    }
    formState.value = row;
    formState.value.folderId = selectedMoveNode.folderId;
    saveFormDesign(formState.value).then(() => {
      moveModal.value = false;
      message.success('操作成功。');
      reload();
    });
  }

  function handleMoveSelect(node) {
    searchInfo.typeFullId = null;
    if (node) {
      selectedMoveNode.folderId = node.id;
      selectedMoveNode.nodeType = node.parentId;
    }
    reload();
  }

  //查询接口
  const getIndexListByParam = async () => {
    let list = await getFormDesignList(searchInfo);
    indexList.value = list;
    return list;
  };

  const [registerTable, { reload, getPaginationRef, getSelectRows, clearSelectedRowKeys }] =
    useTable({
      title: '表单设计',
      // api: getOrgTypeList,
      api: async () => {
        const paginationRef = getPaginationRef() as any;
        searchInfo.pageNo = paginationRef.current;
        searchInfo.pageSize = paginationRef.pageSize;
        let dataObj = await getIndexListByParam();
        return dataObj;
      },
      columns: columns,
      useSearchForm: true,
      formConfig: searchFormSchema,
      showTableSetting: false,
      bordered: true,
      rowKey: 'id',
      pagination: {
        defaultPageSize: 1,
        pageSize: 50,
      },
      rowSelection: {
        type: 'radio',
      },
      handleSearchInfoFn(info) {
        assign(searchInfo, info);
        return info;
      },
      sortFn: (sortInfo: SorterResult) => {
        if (sortInfo.order) {
          searchInfo.sortField = sortInfo.field;
          searchInfo.sortOrder = sortInfo.order.replace('end', '');
        } else {
          searchInfo.sortField = null;
          searchInfo.sortOrder = null;
        }
      },
    });
</script>
<style lang="less" scoped>
  :deep(.left-tree-layout) {
    flex-shrink: 0;
    width: 298px;
    border-radius: 2px;
  }

  .right-table-layout {
    width: calc(100% - 308px);
  }

  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings) {
      margin-right: 0;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
