import { defHttp } from '/@/utils/http/axios';
import { BasicColumn, FormProps } from '/@/components/Table';

enum Api {
  createFormDesignUrl = '/system/form-design/create',
  updateFormDesignUrl = '/system/form-design/update',
  deleteFormDesignUrl = '/system/form-design/delete',

  updateStatusUrl = '/system/form-design/updateStatus',

  getFormDesignListUrl = '/system/form-design/page',
}

// 新增
export const createFormDesign = (params) =>
  defHttp.post({ url: Api.createFormDesignUrl, params }, {});

//修改
export const updateFormDesign = (params) =>
  defHttp.post({ url: Api.updateFormDesignUrl, params }, {});

//保存
export const saveFormDesign = (params) => {
  if (!params.value.id) {
    return createFormDesign(params.value);
  }
  return updateFormDesign(params.value);
};

//删除
export const updateStatus = (id, status) =>
  defHttp.put({ url: Api.updateStatusUrl + '?id=' + id + '&status=' + status }, {});

//删除
export const deleteFormDesign = (id) =>
  defHttp.delete({ url: Api.deleteFormDesignUrl + '?id=' + id }, {});

//查看列表
export const getFormDesignList = (params) =>
  defHttp.get({ url: Api.getFormDesignListUrl, params }, { isOnlyResult: true });

//列表搜索条件
